import React from 'react';
import { useSelector } from 'react-redux';

import orderDetailsStyles from './OrderDetails.module.css';

const OrderDetails: React.FC = () => { 
    const orderId = useSelector((state) => state['order'].orderId);

    return(
        <div className={orderDetailsStyles.orderDetails}>
            <p className={`${orderDetailsStyles.orderNumber} text text_type_digits-large`}>{orderId}</p>
            <p className='text text_type_main-medium'> </p>
            <div className={orderDetailsStyles.image}/>
            <p className={`${orderDetailsStyles.success} text text_type_main-default`}>   </p>
            <p className={`${orderDetailsStyles.text} text text_type_main-default text_color_inactive`}>    </p>
        </div>
    )
}

export default OrderDetails;
